.anna-cascade {
  background-color: #F9FBF9;
  color: #333;
  font-size: 30px;
  text-align: left; }
  .anna-cascade-selected-options {
    min-height: 90px;
    background-color: #FDFFFD;
    padding: 24px 0; }
    .anna-cascade-selected-options-step {
      display: flex;
      padding-bottom: 30px; }
      .anna-cascade-selected-options-step:nth-last-child(1) {
        padding-bottom: 0; }
      .anna-cascade-selected-options-step-line {
        position: relative;
        width: 30px;
        min-width: 30px;
        margin-right: 30px; }
        .anna-cascade-selected-options-step-line-dot {
          position: absolute;
          width: 16px;
          height: 16px;
          top: 30px;
          left: 100%;
          margin-top: -8px;
          margin-left: -8px;
          border-radius: 50%;
          background-color: #E8E8E8;
          z-index: 10; }
        .anna-cascade-selected-options-step-line-dot_line_default::before {
          content: '';
          position: absolute;
          width: 2px;
          height: calc(100% + 30px);
          background-color: #E8E8E8;
          top: 30px;
          left: 100%;
          margin-left: -1px; }
        .anna-cascade-selected-options-step-line-dot_line_active::before {
          content: '';
          position: absolute;
          width: 2px;
          height: calc(100% + 30px);
          background-color: #1890FF;
          top: 30px;
          left: 100%;
          margin-left: -1px; }
      .anna-cascade-selected-options-step-container {
        flex-grow: 1; }
        .anna-cascade-selected-options-step-container-content-main {
          display: flex;
          justify-content: space-between;
          align-items: center; }
          .anna-cascade-selected-options-step-container-content-main-left {
            display: flex;
            align-items: center;
            height: 60px; }
          .anna-cascade-selected-options-step-container-content-main-right {
            display: flex;
            align-items: center;
            padding-right: 20px;
            color: #999; }
            .anna-cascade-selected-options-step-container-content-main-right-text {
              padding-right: 10px; }
            .anna-cascade-selected-options-step-container-content-main-right-arrow {
              min-width: 24px; }
  .anna-cascade-showed-options {
    min-height: 200px;
    padding: 0 30px; }
    .anna-cascade-showed-options-title {
      padding: 30px 0 10px 0;
      font-size: 24px;
      color: #666; }
    .anna-cascade-showed-options-category {
      padding: 30px 0; }
