.anna-tabs-header-titles {
  position: relative;
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
  font-size: 26px; }
  .anna-tabs-header-titles-bg {
    padding: 6px;
    background-color: #EDEDED;
    border-radius: 72px; }
    .anna-tabs-header-titles-bg-container {
      display: flex;
      justify-content: center;
      height: 60px;
      line-height: 60px;
      position: relative;
      z-index: 0; }
      .anna-tabs-header-titles-bg-container-title {
        width: 140px;
        text-align: center;
        transition: all 0.3s;
        overflow: hidden;
        white-space: nowrap; }
      .anna-tabs-header-titles-bg-container-active {
        position: absolute;
        left: 0;
        background-color: #FDFFFD;
        width: 50%;
        height: 100%;
        border-radius: 60px;
        z-index: -1;
        transform: translateX(0);
        transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
        box-shadow: 0 4px 8px #CCCCCC; }
    .anna-tabs-header-titles-bg-square {
      border-radius: 16px; }
      .anna-tabs-header-titles-bg-square .anna-tabs-header-titles-bg-container-active {
        border-radius: 12px; }

.anna-tabs-header-titles-plain {
  display: flex;
  margin-bottom: 10px; }
  .anna-tabs-header-titles-plain-title {
    box-sizing: border-box;
    position: relative;
    height: 72px;
    line-height: 72px;
    white-space: nowrap;
    margin-right: 50px;
    color: #000; }
    .anna-tabs-header-titles-plain-title-active {
      font-weight: 500; }
      .anna-tabs-header-titles-plain-title-active::after {
        position: absolute;
        left: 50%;
        bottom: 0;
        content: '';
        height: 6px;
        border-radius: 6px;
        width: 32px;
        background-color: #1890FF;
        transform: translateX(-50%);
        transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }

.anna-tabs-header-titles-plain-center {
  justify-content: center; }
  .anna-tabs-header-titles-plain-center-title {
    flex: 1;
    text-align: center;
    padding: 0 10px;
    margin-right: 0; }

.anna-tabs-header-titles-card {
  box-sizing: border-box;
  display: flex;
  border: 1PX solid #1890FF;
  margin-bottom: 10px;
  font-size: 28px;
  border-radius: 12px;
  overflow: hidden; }
  .anna-tabs-header-titles-card-title {
    flex: 1;
    box-sizing: border-box;
    height: 56px;
    line-height: 56px;
    white-space: nowrap;
    color: #1890FF;
    text-align: center;
    border-right: 1PX solid #1890FF; }
    .anna-tabs-header-titles-card-title:nth-last-child(1) {
      border-right: none; }
    .anna-tabs-header-titles-card-title-active {
      font-weight: 500;
      color: #FDFFFD;
      background-color: #1890FF; }
