.anna-tag {
  box-sizing: border-box;
  display: inline-block;
  text-align: center;
  color: #333;
  background-color: #F4F4F4;
  border: 2px solid #F4F4F4;
  height: 44px;
  line-height: 40px;
  padding: 0 14px;
  border-radius: 8px;
  font-size: 24px;
  margin-right: 14px; }
  .anna-tag:nth-last-child(1) {
    margin-right: 0; }
  .anna-tag-small {
    height: 40px;
    line-height: 36px;
    padding: 0 12px;
    border-radius: 6px;
    font-size: 22px; }
  .anna-tag-large {
    height: 52px;
    line-height: 48px;
    padding: 0 20px;
    border-radius: 8px;
    font-size: 24px; }
  .anna-tag-info {
    color: #1890FF;
    background-color: #e6f7ff;
    border: 2px solid #91d5ff; }
  .anna-tag-success {
    color: #52c41a;
    background-color: #f6ffed;
    border: 2px solid #b7eb8f; }
  .anna-tag-warning {
    color: #fa541c;
    background-color: #fff2e8;
    border: 2px solid #ffbb96; }
  .anna-tag-error {
    color: #f5222d;
    background-color: #fff1f0;
    border: 2px solid #ffa39e; }
  .anna-tag-checked {
    color: #FDFFFD;
    background-color: #1890FF;
    border: 2px solid #1890FF; }
