.anna-btn {
  position: relative;
  box-sizing: border-box;
  display: inline-block;
  padding: 0 32px;
  background-color: #EDEDEF;
  height: 64px;
  line-height: 60px;
  border: 1PX solid #EDEDEF;
  border-radius: 64px;
  color: #1890FF;
  font-weight: 500;
  font-size: 28px;
  text-align: center;
  white-space: nowrap;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  touch-action: manipulation;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  margin: 0;
  outline: 0;
  cursor: pointer;
  overflow: unset; }
  .anna-btn:not(.anna-btn-disabled):hover .anna-btn-mask {
    box-sizing: border-box;
    position: absolute;
    top: -1PX;
    right: -1PX;
    bottom: -1PX;
    left: -1PX;
    content: '';
    background-color: rgba(255, 255, 255, 0.35);
    border-radius: inherit;
    pointer-events: none;
    transition: background-color 0.3s; }
  .anna-btn:not(.anna-btn-disabled):active .anna-btn-mask {
    box-sizing: border-box;
    position: absolute;
    top: -1PX;
    right: -1PX;
    bottom: -1PX;
    left: -1PX;
    content: '';
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: inherit;
    pointer-events: none;
    transition: background-color 0.3s; }
  .anna-btn:after {
    display: none; }
  .anna-btn-small {
    font-size: 24px;
    padding: 0 24px;
    height: 48px;
    line-height: 44px;
    border-radius: 52px; }
  .anna-btn-large {
    padding: 0 40px;
    height: 80px;
    line-height: 76px;
    border-radius: 80px; }
  .anna-btn-superlarge {
    font-size: 30px;
    height: 84px;
    line-height: 80px;
    border-radius: 84px; }
  .anna-btn-primary {
    border-color: #1890FF;
    background-color: #1890FF;
    color: #FDFFFD; }
  .anna-btn-plain {
    font-weight: 400;
    color: #666;
    border: 1PX solid #DDD;
    background-color: #FDFFFD; }
  .anna-btn-plain.anna-btn-primary {
    border-color: #1890FF;
    color: #1890FF; }
  .anna-btn-hairline {
    line-height: 64px;
    border-width: 0; }
    .anna-btn-hairline:before {
      box-sizing: border-box;
      position: absolute;
      top: -50%;
      right: -50%;
      bottom: -50%;
      left: -50%;
      content: '';
      border: 1PX solid #DDD;
      border-color: inherit;
      border-radius: inherit;
      transform: scale(0.5);
      pointer-events: none; }
  .anna-btn-small.anna-btn-hairline {
    line-height: 48px; }
  .anna-btn-large.anna-btn-hairline {
    line-height: 80px; }
  .anna-btn-superlarge.anna-btn-hairline {
    line-height: 84px; }
  .anna-btn-square {
    padding: 0 30px;
    border-radius: 12px; }
  .anna-btn-square.anna-btn-small {
    padding: 0 14px; }
  .anna-btn-disabled {
    opacity: 0.4;
    transition: opacity .2s; }
  .anna-btn-danger-default {
    color: #ff4d4f; }
  .anna-btn-danger {
    border-color: #ff4d4f;
    background-color: #ff4d4f;
    color: #FDFFFD; }
  .anna-btn-block {
    width: 100%; }
  .anna-btn-loading {
    padding-left: 84px;
    transition: padding 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }
  .anna-btn-loading-icon {
    display: inline;
    padding-right: 16px;
    margin-left: -52px; }

.widthfixbutton {
  height: 80px;
  display: inline-block;
  overflow: hidden; }
  .widthfixbutton .default {
    display: inline-block;
    width: 200%;
    height: 200%;
    font-size: 56px;
    transform: scale(0.5);
    transform-origin: 0 0;
    text-align: center; }
  .widthfixbutton .large {
    line-height: 160px;
    border-radius: 160px; }
  .widthfixbutton .primary {
    background-color: #1890FF;
    color: #FDFFFD; }
  .widthfixbutton .disabled {
    opacity: 0.4;
    color: rgba(255, 255, 255, 0.6) !important; }
