.anna-skeleton-container {
  padding: 20px 0;
  background-color: #FDFFFD;
  visibility: visible;
  opacity: 1;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }
  .anna-skeleton-container-image {
    height: 750px;
    background-color: #EDEDEF;
    margin-bottom: 30px; }
  .anna-skeleton-container-avatar {
    margin: 0 20px;
    display: flex;
    margin-bottom: 30px; }
    .anna-skeleton-container-avatar-left {
      width: 120px;
      height: 120px;
      border-radius: 50%;
      background-color: #EDEDEF;
      margin-right: 30px; }
    .anna-skeleton-container-avatar-right {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: flex-end; }
      .anna-skeleton-container-avatar-right-text {
        height: 32px;
        background-color: #EDEDEF;
        margin-bottom: 30px; }
        .anna-skeleton-container-avatar-right-text:nth-last-child(1) {
          margin-bottom: 0; }
  .anna-skeleton-container-title {
    margin-left: 20px;
    height: 32px;
    width: 300px;
    background-color: #EDEDEF;
    margin-bottom: 30px; }
  .anna-skeleton-container-row {
    margin: 0 20px;
    height: 32px;
    background-color: #EDEDEF;
    margin-bottom: 30px; }

.anna-skeleton-container_active {
  visibility: hidden;
  opacity: 0; }

.anna-skeleton-child_container {
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }

.anna-skeleton-child_container_active {
  visibility: visible;
  opacity: 1; }

.anna-skeleton-active {
  background: linear-gradient(90deg, #EDEDEF 25%, #E6E6E6 37%, #EDEDEF 63%);
  background-size: 400% 100%;
  -webkit-animation: ant-skeleton-loading 1.4s ease infinite;
          animation: ant-skeleton-loading 1.4s ease infinite; }

@-webkit-keyframes ant-skeleton-loading {
  0% {
    background-position: 100% 50%; }
  100% {
    background-position: 0 50%; } }

@keyframes ant-skeleton-loading {
  0% {
    background-position: 100% 50%; }
  100% {
    background-position: 0 50%; } }
