.anna-radio {
  display: inline-block;
  margin-right: 30px;
  font-size: 28px; }
  .anna-radio:nth-last-child(1) {
    margin-right: 0; }
  .anna-radio-container {
    display: flex;
    align-items: center; }
    .anna-radio-container-not_checked {
      box-sizing: border-box;
      width: 38px;
      height: 38px;
      border-radius: 50%;
      border: 1px solid #999; }
    .anna-radio-container-checked {
      box-sizing: border-box;
      width: 38px;
      height: 38px;
      border-radius: 50%;
      border: 2px solid #1890FF;
      padding: 7px; }
      .anna-radio-container-checked-round_fill {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-color: #1890FF; }
    .anna-radio-container-children {
      margin-left: 20px;
      color: #333;
      white-space: nowrap; }
