    display: flex;
    justify-content: space-between; }
    .anna-base-picker-toolbar-cancel {
      padding: 0 24px;
      color: #999; }
    .anna-base-picker-toolbar-title {
      color: #333;
      font-weight: 500; }
    .anna-base-picker-toolbar-confirm {
      padding: 0 24px;
      color: #1890FF; }
  .anna-base-picker-columns {
    position: relative;
    display: flex;
    height: 440px; }
  .anna-base-picker-column {
    flex: 1;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    touch-action: none;
    overflow: hidden;
    cursor: -webkit-grab;
    cursor: grab;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
    .anna-base-picker-column-items {
      transform: translate3d(0, 176px, 0); }
    .anna-base-picker-column-item {
      height: 88px;
      line-height: 88px;
      padding: 0 24px;
      text-align: center; }
  .anna-base-picker-selected {
    box-sizing: border-box;
    position: absolute;
